	var _this
	import {queryArticleById,commentInsert,updateArticleBatch} from '@/static/js/API.js'
	import uParse from '@/components/u-parse/u-parse.vue'
	export default {
		data() {
			return {
				user:{},
				article:{},
				commentMessage:'',    //评论的内容
				articleId:''         //文章的id
			}
		},
		components:{
			uParse
		},
		onLoad(e) {
		    let articleId = e.articleId
			this.getUser()
			this.getArticleContent(articleId)
			this.articleId = articleId
		},
		methods: { 
			
			//获取用户的信息
			getUser(){
				_this = this
				uni.getStorage({
					key:'user',
					success(res) {
						_this.user = res.data
					},
					fail() {
						uni.reLaunch({
							url:'/pages/login/login'
						})
					}
				})
			},
			
			//获取单个文章的内容
			getArticleContent(articleId){
				_this = this
				uni.showLoading({
					title:'获取中',
				})
				.then(()=>{
					let url = `${queryArticleById}?id=${articleId}`
					return uni.request({
						url:url, 
						method:'GET' 
					}) 
				}) 
				.then(res=>{  
					uni.hideLoading()
					console.log("查询到的单个文章信息",res)
					if(res[1].statusCode == 200){
						_this.article = res[1].data.data
						_this.addScanTime()  
					}
				})
				.catch(Error=>{
					console.log("获取单个文章失败",Error)
					uni.showToast({
						title:'获取内容失败',
						icon:'loading',
						duration:1000
					})
				})
			},
			
			preview(src, e) {
				console.log('选中的图片',src)
			  // do something
			},
			navigate(href, e) {
			  // do something
			},
			
			
			//输入评论 
			inputCommentMessage(event){ 
			   this.commentMessage = event.detail.value
			},
			 
			//添加评论
			addComment(){
				_this = this
				if(_this.commentMessage.length >0){
				  uni.showLoading({
					 title:'添加中',
				  })	
				  .then(()=>{
					  uni.request({
					  	 url:commentInsert,
						 method:'POST',
						 data:{ 
							 commentContent:_this.commentMessage,
							 articleId:_this.article.id,
							 userId:_this.user.id
						 }
					  })
				  })
				  .then((res)=>{ 
					  uni.hideLoading()
					  _this.getArticleContent(_this.articleId)
					  _this.commentMessage = ''
				  })
				  .catch(Error =>{
					  console.log("评论失败",Error)
					  uni.showToast({
					  	 title:'网络错误,评论失败',
						 duration:1000,
					     icon:'loading'
					  })
				  })
				}else{
					uni.showToast({
						title:'不能填写空评论哦！',
						duration:1000,
						icon:'none'
					})
				}
			},
			
			
			//增加浏览次数
			addScanTime(){
				_this = this
				uni.request({
					 url:updateArticleBatch,
					 method:'POST',
					 data:[{
						  userId:_this.article.userId,
						  id:_this.article.id,
						  scanTimes:(_this.article.scanTimes + 1)
					 }],
					 header:{
						 'Content-Type':'application/json'
					 }
				})
				.then(res=>{
					if(res[1].data.statusCode == 200){
						 
					}
				})
				.catch(Error=>{
					console.log("增加浏览次数失败",Error)
				})
			},
			
			//不通过文章
			noPassArticle(){
				_this = this
			    uni.showModal({
			    	title:'提示',
					content:'不通过此文章将会删除掉该文章,请慎重',
				    confirmText:'确定',
					confirmColor:'#39B54A',
					showCancel:true,
					cancelText:'再看一下',
					cancelColor:'#DD514C',
					success(res) {
                        if(res.confirm){
							uni.showLoading({
								title:'提交中',
								success() {
									uni.request({
										url:updateArticleBatch,
										method:'POST',
										data:[{
											userId:_this.article.userId,
											id:_this.article.id,
											isCheck:2
										}]
									})
									.then(res=>{
										console.log("审核不通过提交的信息",res)
										uni.hideLoading()
										uni.showToast({
											title:'提交成功',
											duration:500,
											icon:'success',
											success() {
												uni.navigateBack({
													delta:-1
												})
											}
										})
									})
									.catch(Error=>{
										console.log(Error)
										uni.showToast({
											title:'网络错误稍后重试',
											duration:1000,
											icon:'loading'
										})
									})
								}
							})
						}
					}
			    })
			},
			
			//通过文章
			passArticle(){
				_this = this
				uni.showModal({
					title:'提示',
					content:'通过之后文章将发布到校墙，所有人将会看到此文章，请慎重',
				    confirmText:'确定',
					confirmColor:'#39B54A',
					showCancel:true,
					cancelText:'再看一下',
					cancelColor:'#DD514C',
					success(res) {
				        if(res.confirm){
							uni.showLoading({
								title:'提交中',
								success() {
									uni.request({
										url:updateArticleBatch,
										method:'POST',
										data:[{
											userId:_this.article.userId,
											id:_this.article.id,
											isCheck:1
										}]
									})
									.then(res=>{
										console.log("审核通过后返回的信息",res)
										uni.hideLoading()
										uni.showToast({
											title:'提交成功',
											duration:500,
											icon:'success',
											success() {
												uni.navigateBack({
													delta:-1
												})
											}
										})
									})
									.catch(Error=>{
										console.log(Error)
										uni.showToast({
											title:'网络错误稍后重试',
											duration:1000,
											icon:'loading'
										})
									})
								}
							})
						}
					}
				})
			}
		}
	}
